import { _decorator, Component, Node } from 'cc';
import { Piece } from '../Piece/Piece';
import { GridManager } from '../Grid/GridManager';
const { ccclass, property } = _decorator;

@ccclass('GravityHandler')
export class GravityHandler extends Component {

    @property(GridManager)
    private gridManager: GridManager = null;

    applyGravity(matches: Piece[]) {
        const columnPieceCount = new Map<number, number>();
        
        // Sütunlarda kaç adet boşluk oluştuğunu hesapla
        for (const piece of matches) {
            const col = piece.col;
            if (columnPieceCount.has(col)) {
                columnPieceCount.set(col, columnPieceCount.get(col) + 1);
            } else {
                columnPieceCount.set(col, 1);
            }
        }

        this.applyGravityToCol(columnPieceCount);
    }

    applyGravityToCol(columnPieceCount: Map<number, number>) {
        console.log(columnPieceCount);
        
        // for (const [col, emptySpaces] of columnPieceCount) {
        //     this.applyGravityForSingleColumn(col, emptySpaces);
        // }
    }

    applyGravityForSingleColumn(col: number, emptySpaces: number) {
        const grid = GridManager.getInstance().grid;

        for (let row = grid.length - 1; row >= 0; row--) {
            const piece = grid[row][col];

            if (piece.node !== null && piece !== undefined) {
                const newRow = row + emptySpaces;

                if (newRow < grid.length) {
                    console.log(`Moving piece at (row: ${row}, col: ${col}) to (row: ${newRow}, col: ${col})`);

                    // Parçayı yeni yerine taşı
                    piece.updatePosition(0,0);
                    grid[newRow][col] = piece;
                    piece.row = newRow;
                    grid[row][col] = null;
                }
            }
        }

        // Yeni parçalara yer açılması için boş kalan üst alanları doldur
        // for (let i = 0; i < emptySpaces; i++) {
        //     const newPiece = this.gridManager.createPiece(0, col);
        //     grid[i][col] = newPiece;
        //     console.log(`New piece created at (row: ${i}, col: ${col})`);
        // }
    }
}
